export const CartEmptyBlock: FC = () => {
  return (
    <div className='container container--cart'>
      <div className='cart cart--empty'>
        <h2>  </h2>
        <p>
           ,     .
          <br />
           ,   ,    .
        </p>
        <img src={emptyCart} alt='Empty cart' />
        <Link to='/' className='button button--black'>
          <span> </span>
        </Link>
      </div>
    </div>
  )
}
